@import './variables.scss';

@font-face {
    font-family: GothamHTF-Bold;
    src: url("./fonts/GothamHTF-Bold.otf") format("opentype");
}

@font-face {
    font-family: GothamHTF-Book;
    src: url("./fonts/GothamHTF-Book.otf") format("opentype");
}

@font-face {
    font-family: GothamHTF-BookItalic;
    src: url("./fonts/GothamHTF-BookItalic.otf") format("opentype");
}

@font-face {
    font-family: GothamHTF-Medium;
    src: url("./fonts/GothamHTF-Medium.otf") format("opentype");
}

body {
  padding: 0;
  margin: 0;
  font-family: GothamHTF-Book;
}

.font-medium {
  font-family: GothamHTF-Medium;
}

.font-bold {
  font-family: GothamHTF-Bold;
}

.font-italic {
  font-family: GothamHTF-BookItalic;
}

.font-os {
  font-family: "Open Sans";
}

.uppercase {
  text-transform: uppercase;
}

.center {
  display: flex;
  justify-content: center;
}

.vertical-center {
  display: flex;
  align-items: center;
  height: 100%;
}

.right {
  display: flex;
  justify-content: flex-end;
}

#header {
  width: 100vw;
  height: 125px;
  border-bottom: 1px solid $grayD;
  overflow: hidden;
  white-space: nowrap;
  .top-bar {
    height: 35px;
    background-color: black;
    color: white;
    font-size: 0.6em;
    letter-spacing: 1px;
  }
  .bottom-area {
    height: 90px;
  }
  .rings svg {
    position: absolute;
    width: 110px; 
  }
  .header-left-area > span {
    margin-left: 50px;
  }
  .right > span {
    margin-right: 50px;
  }
  .icon {
    margin-right: 50px;
  }
}


