@media only screen and (max-width: 575px) {
  .xs-hidden {
    display: none;
  }
  .bottom-area {
    height: 70px;
  }
  #header {
    height: 115px;
    .rings, .icons {
      transform: scale(0.78, 0.78);
    }
    .icon {
      margin-right: 15px;
    }
    .menu-bars {
      margin-left: 15px;
    }
    .xs-menu > div {
      background-color: white;
      padding: 12px;
      height: 20px;
      border-bottom: 1px solid $grayD;
      z-index: 10;
    }
    .xs-menu {
      height: 60px;
      width: 100px;
      position: absolute;
      margin-top: 15px;
      border: 1px solid $grayD;
      transform: scaleY(0);
      transition: transform 0.5s ease;
      transform-origin: top;
    }
    .active.xs-menu {
      transform: scaleY(1);
    }
    .menu-bars span, .menu-bars span:before, .menu-bars span:after {
      cursor: pointer;
      width: 18px;
      height: 2px;
      background-color: black;
      position: absolute;
      display: block;
      content: '';
    }
    .menu-bars span:before {
      top: -7px; 
    }
    .menu-bars span:after {
      bottom: -7px;
    }

    .menu-bars span, .menu-bars span:before, .menu-bars span:after {
      transition: all 0.5s ease-in-out;
    }
    .menu-bars.active > span {
      background-color: transparent;
    }
    .menu-bars.active span:before, .menu-bars.active span:after {
      top: 0;
    }
    .menu-bars.active span:before {
      transform: rotate(45deg);
    }
    .menu-bars.active span:after {
      transform: rotate(-45deg);
    }
  }

}




